<script>
export default {
	data() {
		return {
			balanceMap: {
				charmValue: 0,
				charmPoints: 0,
				contributionValue: 0,
			},
			balanceMapOld: {
				charmValue: 0,
				charmPoints: 0,
				contributionValue: 0,
			},
			guardians:[]
		}
	},
	created() {
		// 从缓存中获取balanceMapOld
		var balanceMapOld = uni.getStorageSync("balanceMapOld");
		if (balanceMapOld == null) {
			this.balanceMapOld = balanceMapOld;
		}
		// 获取守护者
		var guardians = uni.getStorageSync("guardians");
		if (guardians == null) {
			this.guardians = guardians;
		}

		uni.$on("changeBalanceSelfMap", () => {
            this.balanceMap = this.$system.getBalanceSelfMap();
			setTimeout(() => {
				this.balanceMapOld =  this.balanceMap;
				// 将this.balanceMapOld放入缓存
				uni.setStorageSync("balanceMapOld", this.balanceMapOld);
			}, 1000);
        });

		this.getBalanceSelfMap();
		this.getGuardiansOfSelf();
	},
	methods: {
		getBalanceSelfMap() {
			this.$kt.request.request({
				api: this.$api.system.balance.getSelfMap,
				isNoInterceptor: true,
				stateSuccess: (res) => {
					this.$system.setBalanceMap(res.data);
					this.balanceMap = res.data;
					this.balanceMapOld = res.data;
					// 将this.balanceMapOld放入缓存
					uni.setStorageSync("balanceMapOld", this.balanceMapOld);
				},
			});
		},
		getGuardiansOfSelf() {
			this.$kt.request.request({
				api: this.$api.business.ovo.userCharm.getGuardiansOfSelf,
				isNoInterceptor: true,
				stateSuccess: (res) => {
					this.guardians = res.data;
					// 将this.guardians放入缓存
					uni.setStorageSync("guardians", this.guardians);
				},
			});
		},
	}
}
</script>

